import { Loading,Message,MessageBox } from 'element-ui';
import md5 from 'md5'
import router from '../../router'
// import Jsrsasign from 'jsrsasign'6.2.2
// import privateKey from '../../pem/private_key'
let timeoutData;
// let loadingInstance;

/**
 * 
 * @param request
 * @returns {*}
 */

export const axiosRequest = function (request) {
    // loadingInstance = Loading.service();
    let config = request.params || {};
    let timestamp = (new Date()).valueOf();
    /**
     * login
     * login
     */
    if(!request.url.includes('login')){
        config.__sid = window.sessionStorage.getItem('__sid');
        config.timestamp = timestamp;
        config.signature = md5(window.sessionStorage.getItem('key') + timestamp.toString())
    }
    /**
     * formdata
     * formdataappend
     */
    if (request.data instanceof window.FormData) {
        request.data.append('__sid', config.__sid);
        request.data.append('timestamp', config.timestamp);
        request.data.append('signature', config.signature)
    }
    if (request.timeout) {//AJAX
        timeoutData = window.setTimeout(() => {
            // loadingInstance.close();
            window.clearTimeout(timeoutData);
        }, request.timeout);
    }
    request.params = config
    return request
}

function loadingTip() {
    MessageBox.alert('', '', {
        confirmButtonText: '',
        callback: action => {
        }
    });
}

/**
 * 
 * @param response
 * @returns {*}
 */
export const axiosResponse = function (response) {
    // loadingInstance.close();
    switch (response.status) {
        case 404:
            return response;
        case 413:
            return response;
        case 500:
            loadingTip()
            return response;
        case 502:
            loadingTip()
            return response;
        case 503:
            loadingTip()
            return response;
        case 504:
            loadingTip()
            return response;
        default:
            if (response.status && response.status !== 200) {
                loadingTip()
                return response;
            } else {
                if (!response.data.success) {
                    if (response.data.message === undefined) {
                        MessageBox.alert('', '', {
                            confirmButtonText: ''
                        });
                    } else {
                        if (response.data.code === 'E000202' || response.data.code === 'E000203' || response.data.code === 'E000005' ||  response.data.code === 'E000006') {
                            MessageBox.alert(response.data.message, '', {
                                confirmButtonText: '',
                                callback: action => {
                                    router.push({path: '/login'})
                                }
                            });
                        }else{
                            MessageBox.alert(response.data.message, '', {
                                confirmButtonText: ''
                            });
                        }
                    }
                }
                return response
            }
    }    
}
